import React from 'react';
import { Link, Outlet, useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import '../styles/Layout.css';

interface LayoutProps {
  children?: React.ReactNode;
}

const Layout: React.FC<LayoutProps> = ({ children }) => {
  const { currentUser, logout } = useAuth();
  const navigate = useNavigate();

  const handleLogout = async () => {
    try {
      await logout();
      navigate('/login');
    } catch (error) {
      console.error('Uitloggen mislukt:', error);
    }
  };

  return (
    <div className="layout">
      <header className="header">
        <div className="logo">
          <Link to="/">PitchPro</Link>
        </div>
        <nav className="nav">
          {currentUser ? (
            <>
              <Link to="/dashboard">Dashboard</Link>
              <Link to="/favorites">Favorieten</Link>
              <Link to="/profile">Profiel</Link>
              <button onClick={handleLogout} className="logout-button">Uitloggen</button>
            </>
          ) : (
            <>
              <Link to="/login">Inloggen</Link>
              <Link to="/register">Registreren</Link>
            </>
          )}
        </nav>
      </header>

      <main className="main-content">
        {children || <Outlet />}
      </main>

      <footer className="footer">
        <p>&copy; {new Date().getFullYear()} PitchPro. Alle rechten voorbehouden.</p>
      </footer>
    </div>
  );
};

export default Layout; 